	module.exports = {"data":{"markdownRemark":{"html":"<p>アートホールで上海で春祭りのパーティーを開く。私はそこに多くの知り合いを見た。話をした後、上海で「City Love Story」と呼ばれる映画が2本リリースされた。改革と開放はそれほど深くはなかったが、映画のようだった。</p>\n<p>もう一つはロシアの映画「インターナショナル・ガール」で、ロシアの女の子で、病院で看護師として働き、肉体にも関わらず、スウェーデンと結婚する。祖国。読んだ後、私はとても感情的でした。</p>\n<p>国民はこの点を知った。映画にはいくつかの雄大なロシアの歌があり、悲惨さを感じます。このような音楽に対して、主な音色は非常に明確です。国は強くはなく、国際的な地位にはない。最も不幸なのは自国の国民である。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0027.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0027.md","frontmatter":{"title":"2月7日 月曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0027.md absPath of file >>> MarkdownRemark"}}

